body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background: radial-gradient(
    circle,
    rgba(31, 55, 86, 1) 0%,
    rgba(20, 21, 57, 1) 100%
  );
  font-family: 'Barlow Semi Condensed', sans-serif;

  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.score-main {
  width: 150px;
  height: 115px;
  background-color: white;
  margin-left: 25px;
  margin-right: 25px;
  margin-top: 15px;
  margin-bottom: 15px;
  border-radius: 7px;
}

.score-details {
  padding: 18px;
  margin: auto;
  text-align: center;
}
.score-text {
  color: #2a46c0;
  font-size: 20px;
  font-weight: 600;
}
.score-number {
  font-size: 60px;
  font-weight: 700;
  line-height: 60px;
  color: #3b4363;
}

@media only screen and (max-width: 500px) {
  .score-main {
    width: 80px;
    height: 70px;
  }
  .score-details {
    padding: 10px;
    margin: auto;
    text-align: center;
  }
  .score-text {
    color: #2a46c0;
    font-size: 10px;
    font-weight: 600;
  }
  .score-number {
    font-size: 40px;
    font-weight: 700;
    line-height: 40px;
    color: #3b4363;
  }
}

.head-container {
  padding: 30px;
  opacity: 1;
}
.head-main {
  width: 700px;
  height: 150px;
  margin: auto;
  border-style: solid;
  border-width: 3px;
  border-radius: 7px;
  border-color: #606e85;
  display: flex;
  justify-content: space-between;
  position: relative;
  z-index: 1;
}

.head-main img {
  padding: 20px;
}

