.paper {
  background: linear-gradient(
    90deg,
    hsl(230, 89%, 62%) 0%,
    hsl(230, 89%, 65%) 100%
  );
  box-shadow: 0 9px hsl(230, 54%, 42%);
  width: 150px;
  height: 150px;
  border-radius: 100%;
  display: grid;
  place-items: center;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  cursor: pointer;
}

.paper-center {
  background: white;
  width: 120px;
  height: 120px;
  border-radius: 100%;
  box-shadow: 0 7px inset rgb(216, 216, 216);
  display: grid;
  place-items: center;
}

.paper-center img {
  width: 50px;
}

