  }
  100% {
    box-shadow: 0 15px hsl(230, 54%, 42%),
      0 0 5px 60px hsla(230, 56%, 31%, 0.699),
      0 0 5px 120px hsla(230, 56%, 31%, 0.473),
      0 0 5px 180px hsla(230, 56%, 31%, 0.322);
  }
}

@keyframes pulsatePaper {
  0% {
    box-shadow: 0 15px hsl(230, 54%, 42%),
