.secondRow {
  display: flex;
  justify-content: space-between;
}
.thirdRow {
  display: flex;
  justify-content: space-around;
  margin-top: 60px;
}

@media only screen and (max-width: 500px) {
  .choice-main {
    width: 330px;
    height: 370px;
    background-size: 270px 270px;
  }
  .choice-main .thirdRow {
    margin-top: 30px;
  }
}

.game-main {
  margin: auto;
  display: flex;
  justify-content: center;
  width: 100%;
  text-align: center;
  color: white;
  position: relative;
}
.player-choice {
  width: 300px;
  height: 400px;
  margin-right: 20px;
  line-height: 120px;
  font-size: 25px;
  font-weight: 700;
  position: relative;
  display: grid;
  place-items: center;
  grid-template-rows: 25% auto;
  z-index: 0;
}

.player-choice-title {
  z-index: 1;
}

.house-choice {
  width: 300px;
  height: 400px;
  margin-left: 20px;
  font-size: 25px;
  font-weight: 700;
  position: relative;
  display: grid;
  place-items: center;
  grid-template-rows: 25% auto;
  z-index: 0;
}

.house-choice-title {
  z-index: 1;
}

.house-choosing {
  opacity: 0.5;
  background: white;
  width: 270px;
  height: 270px;
  border-radius: 100%;

  -webkit-animation-name: loading;

          animation-name: loading;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.display-winner {
  width: 300px;
  height: 400px;
  padding-top: 180px;
  font-size: 50px;
  font-weight: bold;
  position: relative;
  opacity: 1;
  z-index: 1;
}

.play-again {
  font-family: 'Barlow Semi Condensed', sans-serif;
  font-size: 20px;
  font-weight: 600;
  border: none;
  background-color: white;
  width: 220px;
  height: 50px;
  margin: 20px;
  border-radius: 10px;
  color: hsl(229, 25%, 31%);
  cursor: pointer;
}

@-webkit-keyframes loading {
  0% {
    background-color: rgb(56, 56, 56);
  }
  10% {
    background-color: rgb(58, 58, 58);
  }
  20% {
    background-color: rgb(60, 60, 60);
  }
  30% {
    background-color: rgb(62, 62, 62);
  }
  40% {
    background-color: rgb(64, 64, 64);
  }
  50% {
    background-color: rgb(66, 66, 66);
  }
  60% {
    background-color: rgb(68, 68, 68);
  }
  70% {
    background-color: rgb(70, 70, 70);
  }
  80% {
    background-color: rgb(72, 72, 72);
  }
  100% {
    background-color: rgb(74, 74, 74);
  }
}

@keyframes loading {
  0% {
    background-color: rgb(56, 56, 56);
  }
  10% {
    background-color: rgb(58, 58, 58);
  }
  20% {
    background-color: rgb(60, 60, 60);
  }
  30% {
    background-color: rgb(62, 62, 62);
  }
  40% {
    background-color: rgb(64, 64, 64);
  }
  50% {
    background-color: rgb(66, 66, 66);
  }
  60% {
    background-color: rgb(68, 68, 68);
  }
  70% {
    background-color: rgb(70, 70, 70);
  }
  80% {
    background-color: rgb(72, 72, 72);
  }
  100% {
    background-color: rgb(74, 74, 74);
  }
}
@media only screen and (max-width: 1000px) {
  .house-choice {
    margin-left: 15px;
    height: 300px;
  }
  .player-choice {
    margin-right: 15px;
    height: 300px;
  }

  .house-choosing {
    width: 225px;
    height: 225px;
  }
  .display-winner {
    padding-top: 100px;
  }
}

@media only screen and (max-width: 850px) {
  .house-choice {
    margin-left: 10px;
    height: 200px;
  }
  .player-choice {
    margin-right: 10px;
    height: 200px;
  }

  .house-choosing {
    width: 225px;
    height: 225px;
  }
  .player-choice-title {
    font-size: 15px;
  }
  .house-choice-title {
    font-size: 15px;
  }
  .display-winner {
    padding-top: 80px;
  }
}

@media only screen and (max-width: 500px) {
  .player-choice {
    width: 170px;
    height: 200px;
    position: relative;
    grid-template-rows: 100%;
  }
  .house-choice {
    height: 200px;
    width: 170px;
    grid-template-rows: 100%;
    position: relative;
  }

  .display-winner {
    margin-top: 25vh;
    position: absolute;
    height: 100px;
  }
  .player-choice-title {
    position: absolute;
    margin-top: 250px;

    font-size: 15px;
  }
  .house-choice-title {
    position: absolute;
    margin-top: 250px;
    font-size: 15px;
  }
  .house-choosing {
    width: 140px;
    height: 140px;
  }
}

.modal {
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
  height: 100%;
  width: 100%;
  transition: 1.1s ease-out;
  -webkit-filter: blur(0);
          filter: blur(0);
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
  visibility: visible;
  margin: auto;
  position: fixed;
  z-index: 2;
  left: 0;
  top: 0;
  display: flex;
}
.modal.off {
  opacity: 0;
  visibility: hidden;
  -webkit-filter: blur(8px);
          filter: blur(8px);
  -webkit-transform: scale(0.33);
          transform: scale(0.33);
  box-shadow: 1rem 0 0 rgba(0, 0, 0, 0.2);
}

.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  border-radius: 10px;
  position: relative;
  z-index: 2;
}

.modal-content img {
  margin: auto;
  padding: 15px;
  padding-top: 25px;
  width: 300px;
}

.actions {
  display: flex;
  justify-content: space-between;
}

.actions button {
  border: none;
  background: none;
  font-family: 'Nunito', sans-serif;
  font-size: 25px;

  color: rgb(192, 192, 192);
}

.rules-text {
  font-size: 25px;
  font-weight: 700;
  color: #3b4363;
}

@media only screen and (max-width: 500px) {
  .modal {
    background-color: white;
    transition: none;
  }
  .modal-content {
    border: none;
    padding-bottom: 150px;
  }
  .modal-content img {
    margin: auto;
    padding: 15px;
    padding-top: 25px;

    width: 300px;
  }
  .actions button {
    border: none;
    background: none;
    font-family: 'Nunito', sans-serif;
    font-size: 25px;
    position: absolute;
    color: rgb(192, 192, 192);
    margin-top: 70vh;
    cursor: pointer;
  }
  .actions {
    display: flex;
    justify-content: center;
  }
}

.rulesButton-main {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100px;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.rules-button {
  border: none;
  background: none;
  width: 130px;
  height: 40px;
  font-weight: 600;
  border-style: solid;
  border-width: 2px;
  border-radius: 7px;
  border-color: #b4b4b4;
  margin-right: 30px;
  display: grid;
  place-items: center;
  cursor: pointer;
  background-color: rgb(21, 23, 61);
  color: rgb(221, 221, 221);
}

@media only screen and (max-width: 500px) {
  .rulesButton-main {
    justify-content: center;
  }
  .rules-button {
    margin: auto;
  }
}

